import React, { CSSProperties } from 'react';
import { Card } from '../../../components';
import { UsersInfo } from '../../../api/user';
import UserInfoInterface from '../../../api/interfaces/UserInfo';
import { Link } from 'react-router-dom';

interface AttendentListProps {
    users: UsersInfo | null,
}
const avatar_size = 60;
const index = (props: AttendentListProps) => {
    
    const getUserAvatarUrl = (user:UserInfoInterface) =>{
        if(user.avartar_url && user.avartar_url !== ""){
            return user.avartar_url;
        }
        else{
            return `https://avatars1.githubusercontent.com/u/${user.id}?s=${avatar_size * 2}`;
        }
    }
    return (
        <Card title=" " desc="    ">
            {/*   */}
            <div style={styles.wrapper}>
                {
                    (() => {
                        if (props.users && props.users.data) {
                            return props.users.data.map(user => {
                                return (<div key={ user.id.toString() } style={styles.itemContainer}>
                                    <div style={styles.avatarWrapper}>
                                        {/*  */}
                                        <img style={ styles.avatar } src={ getUserAvatarUrl(user).toString() } alt={ user.login + " github avatar image" }/>
                                    </div>
                                    <div style={styles.descWrapper}>
                                        {/*  */}
                                        <p style={styles.username}>{user.name}</p>
                                        <p style={styles.displayName}>{user.login}</p>
                                    </div>
                                    <div style={styles.linkWrapper}>
                                        {/* <a style={styles.link} target="_blank" rel="noopener noreferrer" href={user.html_url.toString()}></a> */}
                                        <Link style={ styles.link } to={ `/users/${user.login}` }></Link>
                                    </div>
                                </div>)
                            });
                        } else {
                            return <div>
                                <p>  </p>
                            </div>;
                        }

                    })()
                }
            </div>
        </Card>
    );
}

const styles: { [name: string]: CSSProperties } = {
    wrapper: {
        height: '440px',
        overflowY: 'auto',
        marginBottom: '20px',
    },
    itemContainer: {
        display: 'flex',
        flexDirection: 'row',
        marginBottom: '15px',
        borderBottom: '1px solid rgba(0,0,0,.1)',
        paddingBottom: '15px',
    },
    avatarWrapper: {
        // flex: 1,
        display:"flex",
        width:`${avatar_size}px`,
        height:`${avatar_size}px`,
        justifyContent:'center',
        alignContent:'center',
        overflow:'hidden',
        borderRadius:'50%',
        boxShadow: '1px 1px 3px rgba(0,0,0,0.2)'
    },
    avatar:{
        width:`${avatar_size}px`,
        height:`${avatar_size}px`,
    },
    username: {
        fontSize: '1.3em',
        margin: 0,
        padding: 0,
        color: "#fff",
    },
    displayName: {
        fontSize: '.8em',
        margin: 0,
        padding: 0,
        color: "#fff",
    },
    descWrapper: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        padding: '0 10px',
        flex: 3,
    },
    linkWrapper: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flex: 1,
    },
    link: {
        textDecoration: 'none',
        fontSize: '.9em',
        color: 'white',
        padding: '5px 15px',
        backgroundColor: '#27ae60',
        height: 'fit-content',
        borderRadius: '5px',
        boxShadow: '1px 1px 3px rgba(0,0,0,0.2)'
    }
}

export default index;